<template>
  <div id="note" class="detail">
    <note-sidebar :curNote="curNote" @update:notes="val=> notes = val"></note-sidebar>
    <div class="note-detail">
      <div class="note-empty" v-if="!curNote.id">请选择笔记</div>
      <div v-else class="note-detail-ct">
        <div class="note-bar">
          <span>创建日期：{{curNote.createdAtFriendly}}</span>
          <span>更新日期：{{curNote.updatedAtFriendly}}</span>
          <span class="status" :class="{'status-input':isInput,'status-wrong':isWrong}">{{statusText}}</span>
          <span class="iconfont icon-delete" @click="onDeleteNote"></span>
          <span class="iconfont icon-fullscreen" @click="isShowPreview=!isShowPreview"></span>
        </div>
        <div class="note-title">
          <input type="text" v-model="curNote.title" @input="onUpdateNote" @keydown="status" placeholder="输入标题"></input>
        </div>
        <div class="editor">
          <textarea v-show="!isShowPreview" v-model="curNote.content" @input="onUpdateNote"  @keydown="status"></textarea>
          <div class="preview markdown-body" v-html="previewContent" v-show="isShowPreview">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import NoteSidebar from './NoteSidebar'
  import Auth from '../apis/auth'
  import Bus from  '../helpers/bus'
  import _ from 'lodash'
  import  MarkdownIt from 'markdown-it'
  import {mapActions, mapGetters} from 'vuex'


  let md = new MarkdownIt();

  export default {
    name: 'NoteDetail',
    components: {NoteSidebar},
    data () {
      return {
        statusText:'笔记未更改',
        isInput:false,
        isWrong:false,
        isShowPreview:false
      }
    },
    computed:{
      ...mapGetters([
        'curNote',
        'notes'
      ]),
      previewContent(){
        return md.render(this.curNote.content)
      }
    },
    methods:{
      ...mapActions([
        'updateNote',
        'deleteNote',
        'checkLogin'
      ]),

      onUpdateNote:_.debounce(function () {
        this.updateNote({noteId:this.curNote.id,title:this.curNote.title,content:this.curNote.content})
        .then(data=>{
          this.isWrong=false
          this.isInput=false
          this.statusText='笔记已保存'
        }).catch(res=>{
          this.statusText='保存出错'
          this.isWrong=true
        })
      },300),
      onDeleteNote(){
        this.deleteNote({noteId:this.curNote.id})
        .then(()=>{
          this.$router.replace('/note')
        })
      },
      status(){
        this.isWrong=false
        this.isInput=true
        this.statusText='正在输入...'
      }
    },

    created() {
      this.checkLogin({path: '/login'})
        // Bus.$on('update:notes', (val)=>{
        //   this.curNote=val.find(note => note.id.toString()===this.$route.query.noteId)||{}})
    },
    beforeRouteUpdate(to,from,next){
      this.$store.commit('setCurNote',{curNoteId:to.query.noteId})
      next()
    },

  }
</script>

<style lang="less" scoped>
  @import url(../assets/css/note-detail.less);


  #note {
    display: flex;
    align-items: stretch;
    background-color: #fff;
    flex: 1;
  }

</style>